*{
  margin:0;
  padding:0;
}

body{
  background:rgb(231, 236, 236);
  font-family: 'Poppins', sans-serif;

}

.title{
  text-align: center;
  margin-top:4%;
  margin-bottom: 3%;
}

.container{
  
  width: 40%;
  margin: 0 auto;
  background:white;
  margin-bottom: 3%;
  text-align: center;
  -webkit-box-shadow: 0px 0px 17px 0px rgba(50, 50, 50, 0.41);
-moz-box-shadow:    0px 0px 17px 0px rgba(50, 50, 50, 0.41);
box-shadow:         0px 0px 17px 0px rgba(50, 50, 50, 0.41);
  
  
}

@media screen and (max-width: 1120px){
  .container{
    width: 60%;
        
  }
}

@media screen and (max-width: 620px){
  .container{
    width: 70%;
        
  }
}

@media screen and (max-width: 531px){
  .container{
    width: 80%;
        
  }
}

@media screen and (max-width: 410px){
  .container{
    width: 90%;
        
  }
}

.container img{
  
  width: 100%;
}

.container footer{
  padding: 5%;
}

.container footer p{
  text-align: justify;
  margin: 3% 0%;
  line-height: 16pt;
}

.container footer .parent{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.container footer .child1{
  width:80%;
  text-align: left;
  
}

.container footer .child2{
  width:8%;
  text-align: right;
  color: rgb(181, 123, 214);
  
}

@media screen and (max-width: 500px){
  .container footer .child2{
    width:14%;
    
    
  }
}

.child2 h4{
  background: rgb(245, 242, 247);
}

.container footer .not{
  width: 40%;
  padding: 1%;
  background: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 3%;
  font-family: 'Vidaloka', serif;
  letter-spacing: 1pt;
  
}

@media screen and (max-width: 400px){
  .container footer .not{
    width: 60%;
  }
}

.container footer .not:hover{
  /* background: rgb(245, 242, 247); */
  /* color:rgb(181, 123, 214); */
  /* text-decoration: underline; */
  border: double;

}

.readMore{
  border:none;
  background:none;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  
}
.underline{
  
  height: 3px;
  width: 100px;
  margin:.5% auto 0%;
  background: blue;
}

.refresh{
  width: 20%;
  padding: 1%;
  background: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 3%;
  font-family: 'Vidaloka', serif;
  letter-spacing: 1pt;
}

.refresh:hover{
  border: double;
}

.owner{
  text-align: center;
  margin-bottom: 4%;
  font-size: 14px;
  text-decoration: underline;
}